<template>
    <div class="list" ref="wrapper">
      <div>
        <div class="area">
          <div class="title border-top-bottom">當前城市</div>
          <div class="button-list">
            <div class="button-wrapper">
                <div class="button">{{this.currentCity}}</div>
            </div>
          </div>
        </div>
        <div class="area">
            <div class="title border-top-bottom">熱門城市</div>
              <div class="button-list">
              <div
                class="button-wrapper"
                v-for="item of hot"
                :key="item.id"
                @click="handleCityClick(item.name)"
              >
                  <div class="button">{{item.name}}</div>
              </div>
            </div>
        </div>
        <div class="area" v-for="(item, key) of cities" :key="key">
            <div class="title border-top-bottom">
              {{key}}
            </div>
            <div class="item-list">
                <div class="item border-bottom"
                v-for="innerItem of item"
                :key="innerItem.id"
                @click="handleCityClick(innerItem.name)"
            >
                {{innerItem.name}}
                </div>
            </div>
        </div>
      </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import Bscroll from 'better-scroll'
export default {
  name: 'CityList',
  props: {
    hot: Array,
    cities: Object
  },
  methods: {
    handleCityClick (city) {
      this.changeCity(city)
      this.$router.push('/')
    },
    ...mapMutations(['changeCity'])
  },
  computed: {
    ...mapState({
      currentCity: 'city'
    })
  },
  mounted () {
    this.scroll = new Bscroll(this.$refs.wrapper, {click: true})
  },
  updated () {
    this.scroll.refresh()
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/iconfont/variables.styl'
  .border-top-bottom
    &:before
        border-color: #ccc
    &:after
        border-color: #ccc
  .border-bottom
      &:before
        border-color: #ccc
  .list
    overflow: hidden
    position: absolute
    top: 1.58rem
    left: 0
    right: 0
    bottom: 0
    .title
      line-height: .54rem
      background: #eee
      padding-left: .2rem
      color: #666
      font-size: .26rem
    .button-list
      overflow: hidden
      padding: .1rem
      .button-wrapper
        float: left
        width: 33.33%
      .button
        margin: .1rem
        padding: .1rem 0
        text-align: center
        border: .02rem solid #ccc
        border-radius: .06rem
    .item-list
      .item
        line-height: .76rem
        padding-left: .2rem
</style>



